@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap);
* {
  
  font-family: 'Montserrat', 'Big Shoulders Text' sans-serif;
}
body{
    background:linear-gradient(150deg, rgb(2, 49, 119), rgb(51, 87, 245));
    height: 30rem;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.Info-container{
    margin-top: 3rem;
}
.resp-photo{
    width:22rem;
    display:block;
}
.presentation{
    color:#ffffff;
    font-family: 'Big Shoulders Text';
    text-align: left;
}
.br-div:hover{
    border: solid 1px gray;
}
.h5size{
    font-size:1.5rem;
}
.h1size{
    font-size: 3rem;
    font-weight: 700;
}
.h3size{
    font-size:2.5rem;
    font-style: italic;
    font-weight: 600;
    color:aquamarine;
}
.h4size{
    font-size: 2rem;
}
@media screen and (min-width: 64rem){
    .Info{
    margin:0;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    }
    .resp-photo:hover{
        width:24rem;
        border-radius: 50%;
        border: solid 6px rgba(97, 5, 139,0.7);
    }
    .h1size:hover{
        font-size: 4rem;
        color: rgba(61, 238, 238, 0.849);
        
    }
}
@media screen and (min-width:41rem) and (max-width:63rem){
    .resp-photo{
        width:18rem;
    }
    .resp-photo:hover{
        width:20rem;
        border-radius:50%;
        border: solid 6px rgba(97, 5, 139,0.7);
    }
}
@media screen and (max-width:40rem){
    .resp-photo{
        width:14rem;
    }
    .resp-photo:hover{
        width:17rem;
        border-radius: 50%;
        border: solid 6px rgba(97, 5, 139,0.7);
    }
    .h5size{
        font-size:1rem;
    }
    .h1size{
        font-size: 2rem;
        font-weight: 700;
    }
    .h1size:hover{
        font-size: 3rem;
        color: rgba(61, 238, 238, 0.849);
    }
    .h3size{
        font-size:1.8rem;
        font-style: italic;
        font-weight: 600;
    }
    .h4size{
        font-size: 1.3rem;
    }
}
@media screen and (max-width:480px){
.skill-title{
    font-size:2rem;
}
.soft-skill{
    font-size:1.5rem;
    margin-right: 2.5rem;
}
.tech-skill{
    font-size:2rem;
    margin-right: 1.3rem;
}
.tech-name{
    font-size:1rem;
}
.name-mov{
    margin-left:0.9rem!important;
}
.b-name{
    margin-left:-15px!important;
}
.name-mov2{
    margin-left: 1.4rem !important;
}
}
@media screen and (min-width:1024px){
    .skill-title{
        font-size: 3rem;
    }
    .soft-skill{
        font-size: 2rem;
    }
    .tech-skill{
        font-size:4rem;
        margin-right:3rem;
    }
    .tech-name{
        font-size:1.5rem;
    }
    .name-mov{
        margin-left:4rem!important;
    }
    .name-mov2{
        margin-left:2.5rem!important;
    }
}
@media screen and (min-width:481px) and (max-width:1023px){

}

.Skills{
    margin-top: 3rem;
}
.skill-title{
    color: #fff;
    text-align: center;
    font-family: 'Big Shoulders Text';
}
.soft-skill{
    color: #fff;
    text-align: center;
    font-family: 'Big Shoulders Text';
    list-style: none;
}
.tech-name{
    color:#fff;
    font-family: 'Big Shoulders Text';
    margin-left:10px; 
}






.home-img{
    width:6.5rem;
}

.navbar-static{
    width: 100%;
    position: fixed!important;
    z-index: 100;
    top: 0;
}
.separation{
    margin-left: 800px;
}
.bg-dark2{
    background:linear-gradient(150deg, rgb(2, 49, 119), rgb(51, 87, 245))!important;
}
.img-fluid{
    margin-left:15px!important;
}

@media screen and (max-width:40rem){
    .separation{
        margin-left: 0;
    }
}
